<template>
    <view>

        <view style="
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index:-1;
        background-color: #F5f5f5;
        "></view>

        <view :style="'height: ' + navBarHeight + 'px;'"></view>

        <scroll-view scroll-y :style="'height: calc(100vh - ' + navBarHeight + 'px;)'">
            <view class="scroll-box">
                <user-card :ovoUserInfo="userSelf"></user-card>

                <view style="height: 20rpx;"></view>

                <u-grid :col="4">

                    <u-grid-item
                        @click="toMyExchange('all')"
                    >
                        <view
                         class="u-grid-item-content">
                            <view class="u-grid-item-content-num">{{ exchangeMapSelf.allNum }}</view>
                            <view class="u-grid-item-content-text">全部</view>
                        </view>
                    </u-grid-item>

                    <u-grid-item
                    @click="toMyExchange('notSend')"
                    >
                        <view class="u-grid-item-content">
                            <view class="u-grid-item-content-num">{{ exchangeMapSelf.notSendNum }}</view>
                            <view class="u-grid-item-content-text">未发货</view>
                        </view>
                    </u-grid-item>

                    <u-grid-item
                    @click="toMyExchange('send')"
                    >
                        <view class="u-grid-item-content">
                            <view class="u-grid-item-content-num">
                            {{ exchangeMapSelf.sendNum }}
                            </view>
                            <view class="u-grid-item-content-text">待收货</view>
                        </view>
                    </u-grid-item>

                    <u-grid-item
                    @click="toMyExchange('receive')"
                    >
                        <view class="u-grid-item-content">
                            <view class="u-grid-item-content-num">
                            {{ exchangeMapSelf.receiveNum }}
                            </view>
                            <view class="u-grid-item-content-text">已收货</view>
                        </view>
                    </u-grid-item>


                </u-grid>
                <view style="height: 20rpx;"></view>
                <!-- <view style="height: 20rpx;
                    border-bottom: 1rpx solid #eee;
                    border-top: 1rpx solid #eee;
                    bakcground-color: #fff;
                    "></view> -->

            </view>

            <view style="height: 20rpx;"></view>

            <view>


                <!-- 收货地址 -->
                <view class="scroll-box" style="border-radius: 0;">

                    <view class="scroll-box-title">默认收货地址

                        <image v-if="defaultAddress" @click="toEditAddress" :src="$kt.file.byPath('image/icon/edit.svg')"
                            :style="{
                                width: '40rpx',
                                height: '40rpx',
                            }" style="float: right;" class="icon-btn"></image>
                    </view>

                    <goods-default-address ref="goodsAddress"></goods-default-address>


                </view>


            </view>



        </scroll-view>
    </view>
</template>


<script>
import shopGoods from './inPageExchange/shopGoods.vue';

export default {
    components: {
        shopGoods
    },

    props: {
        navBarHeight: {
            type: Number,
            default: 0
        },
    },
    data() {
        return {
            segmentHeight: 0,
            userSelf: {},
            defaultAddress: null,
            // 是否正在加载defaultAddress
            isLoadingDefaultAddress: true,
            exchangeMapSelf: {
                "allNum": 1,
                "notSendNum": 1,
                "sendNum": 0,
                "receiveNum": 0,
            }
        }
    },
    created() {
        this.userSelf = this.$ovo.getSelf();

        uni.$on('changeOvoSelfInfo', (userSelf) => {
            this.userSelf = userSelf;
        });

        this.getDefalutAddress();
        this.getExchangeMapSelf();
        uni.$on("shopPageMyInfoRefresh",()=>{
            this.getExchangeMapSelf();
        });
    },
    mounted() {

    },
    methods: {
        toMyExchange(sendStatusCode){
			uni.navigateTo({
				url:'/pages/shop/publich/my-exchange?sendStatusCode='+sendStatusCode
			})
		},

        toEditAddress() {
            // #ifdef MP-WEIXIN

            uni.chooseAddress({
                success: (res) => {
                    console.log(JSON.stringify(res));
                    this.$kt.request.request({
                        api: this.$api.system.address.save,
                        data: {
                            userName: res.userName,
                            telNumber: res.telNumber,
                            provinceName: res.provinceName,
                            cityName: res.cityName,
                            countyName: res.countyName,
                            detailInfo: res.detailInfo,
                            nationalCode: res.nationalCode,
                            postalCode: res.postalCode,
                            isDefault: true,
                        },
                        stateSuccess: (res) => {
                            this.getDefalutAddress();
                        },
                        fail: (error) => { }
                    });

                },
                fail: (error) => { }
            })

            // #endif

        },

        getDefalutAddress() {
            this.isLoadingDefaultAddress = true;
            // 从缓存中获取默认地址
            let defaultAddress = uni.getStorageSync('defaultAddress');

            if (defaultAddress) {
                this.defaultAddress = defaultAddress;
                this.isLoadingDefaultAddress = false;
            }

            this.$kt.request.request({
                api: this.$api.system.address.getDefault,
                stateSuccess: (res) => {
                    this.defaultAddress = res.data;
                    if (!this.defaultAddress) {
                        this.isLoadingDefaultAddress = false;
                        return null;
                    }

                    this.$refs.goodsAddress.setAddress(res.data);

                    uni.$emit('changeDefaultAddress', this.defaultAddress);

                    this.isLoadingDefaultAddress = false;
                }
            });
        },

        getExchangeMapSelf() {
            this.$kt.request.request({
                api: this.$api.business.ovoShop.goodsExchange.getMapSelf,
                stateSuccess: (res) => {
                    this.exchangeMapSelf = res.data;
                }
            });
        },

    },
}

</script>

<style lang="scss" scoped>
.scroll-box {
    box-sizing: border-box;
    width: 100%;
    position: relative;
    padding: 20rpx;
    background-color: #fff;
}

.u-grid-item-content {
    text-align: center;

    .u-grid-item-content-num {
        font-size: 30rpx;
        color: #333;
    }

    .u-grid-item-content-text {
        font-size: 28rpx;
        color: #666;
    }
}

.box-box {
    box-sizing: border-box;
    width: 100%;
    position: relative;
    padding: 20rpx;
    background-color: #fff;
    border-radius: 20rpx;
    font-size: 30rpx;
    border: 5rpx solid #eee;
    margin-top: 10rpx;

    .de-text {
        font-size: 24rpx;
        margin-left: 10rpx;
    }

}

.scroll-box-title {
    font-size: 26rpx;
    color: #333;
}

.scroll-box-item {
    box-sizing: border-box;
    width: 100%;
    position: relative;
    padding: 20rpx 0 20rpx 0;
    background-color: #fff;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    // border-bottom: 1rpx solid #eee;

    .scroll-box-item-title {
        font-size: 28rpx;
        color: #333;
    }

    .scroll-box-item-content {
        font-size: 28rpx;
        color: #666;
    }

}


.icon-btn:active {
    transform: scale(0.9);
}
</style>